.loading[data-v-410bfe5f] {
	height: 30px;
	width: 100px;
  display: block;
  margin: auto;
	padding-left: 25px;
	padding-top: 30px;
}
.blob[data-v-410bfe5f] {
	border-radius: 50%;
	background-color: whitesmoke;
	display: block;
	float: left;
	margin: 5px 2px;
	position: relative;
	top: 5px;
	height: 20px;
	width: 20px;

	-webkit-animation: loading-blob-data-v-410bfe5f 1.2s infinite;
	animation:         loading-blob-data-v-410bfe5f 1.2s infinite;

	-webkit-animation-timing-function: ease-in-out;
	animation-timing-function: ease-in-out;

	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}
@-webkit-keyframes loading-blob-data-v-410bfe5f {
0% { height: 20px; top: 5px; width: 20px;
}
25% { height: 30px; top: 0; width: 30px;
}
50% { height: 20px; top: 5px; width: 20px;
}
100% { height: 20px; width: 20px;
}
}
@keyframes loading-blob-data-v-410bfe5f {
0% { height: 20px; top: 5px; width: 20px;
}
25% { height: 30px; top: 0; width: 30px;
}
50% { height: 20px; top: 5px; width: 20px;
}
100% { height: 20px; width: 20px;
}
}
.blob2[data-v-410bfe5f] {
	-webkit-animation-delay: 150ms;
	animation-delay: 150ms;
}
.blob3[data-v-410bfe5f] {
	-webkit-animation-delay: 300ms;
	animation-delay: 300ms;
}

